@font-face {
  font-family: 'fontello';
  src: url('/assets/fonts/fontello.eot?90822702');
  src: url('/assets/fonts/fontello.eot?90822702#iefix')
      format('embedded-opentype'),
    url('/assets/fonts/fontello.woff2?90822702') format('woff2'),
    url('/assets/fonts/fontello.woff?90822702') format('woff'),
    url('/assets/fonts/fontello.ttf?90822702') format('truetype'),
    url('/assets/fonts/fontello.svg?90822702#fontello') format('svg');
  font-weight: normal;
  font-style: normal;
}
/* Chrome hack: SVG is rendered more smooth in Windozze. 100% magic, uncomment if you need it. */
/* Note, that will break hinting! In other OS-es font will be not as sharp as it could be */
